"use client"

import { usePathname, useRouter } from "next/navigation"
import { Home, ShoppingBag, Clock, User } from "lucide-react"
import { cn } from "@/lib/utils"
import { useAuthStore } from "@/lib/store"

export function BottomNav() {
  const router = useRouter()
  const pathname = usePathname()
  const { isAuthenticated } = useAuthStore()

  const navItems = [
    { icon: Home, label: "In√≠cio", path: "/client" },
    { icon: ShoppingBag, label: "Mercado", path: "/client/market" },
    { icon: Clock, label: "Pedidos", path: "/client/orders", requireAuth: true },
    { icon: User, label: "Perfil", path: "/client/profile", requireAuth: true },
  ]

  const handleNavigation = (path: string, requireAuth?: boolean) => {
    if (requireAuth && !isAuthenticated) {
      router.push("/login")
    } else {
      router.push(path)
    }
  }

  return (
    <nav className="fixed bottom-0 left-0 right-0 z-20 border-t bg-background">
      <div className="flex items-center justify-around py-2">
        {navItems.map((item) => {
          const Icon = item.icon
          const isActive = pathname === item.path
          return (
            <button
              key={item.path}
              onClick={() => handleNavigation(item.path, item.requireAuth)}
              className={cn(
                "flex flex-col items-center gap-1 px-4 py-2 transition-colors",
                isActive ? "text-primary" : "text-muted-foreground",
              )}
            >
              <Icon className="h-5 w-5" />
              <span className="text-xs font-medium">{item.label}</span>
            </button>
          )
        })}
      </div>
    </nav>
  )
}
